import React, { useState } from "react";
import "./ContactContainer.scss";
import { Alert, Button, Grid, TextField, createTheme } from "@mui/material";
import { ThemeProvider } from "@emotion/react";
import { LoadingState, Mail } from "../../datatypes/bindings";
import { ApiClient } from "../../datatypes/apiClient";

const theme = createTheme({
  palette: {
    mode: "light",
  },
});

interface ContactProps {
  apiClient: ApiClient;
}

export const ContactContainer: React.FunctionComponent<ContactProps> = (
  props: ContactProps
) => {
  const { apiClient } = props;
  const [emailLoadingState, setEmailLoadingState] = useState<LoadingState>(
    LoadingState.NOT_LOADED
  );
  const [isFormDirty, setIsFormDirty] = useState(false);
  const [mail, setMail] = useState<Mail>({
    name: "",
    email: "",
    message: "",
  });

  const onChangeMail = (field: string, data: string) => {
    setIsFormDirty(true);
    setMail({
      ...mail,
      [field]: data,
    });
  };

  const validateName = () => {
    return mail.name && mail.name.length > 0;
  };

  const validateEmail = () => {
    return (
      mail.email &&
      mail.email.length > 0 &&
      mail.email.match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      )
    );
  };

  const validateMessage = () => {
    return mail.message && mail.message.length > 0;
  };

  const onSubmit = (e: any) => {
    e.preventDefault();
    if (validateName() && validateEmail() && validateMessage()) {
      setEmailLoadingState(LoadingState.LOADING);
      apiClient.sendTalkRequest(mail).then(() => {
        setEmailLoadingState(LoadingState.LOADED);
        setMail({
          name: "",
          email: "",
          message: "",
        });
      });
    }
  };

  return (
    <div id="contact">
      <ThemeProvider theme={theme}>
        {emailLoadingState === LoadingState.LOADED && (
          <Alert
            id="request-msg"
            color="success"
            onClose={() => setEmailLoadingState(LoadingState.NOT_LOADED)}
          >
            Email sent successfully, I'll get you back soon!
          </Alert>
        )}
      </ThemeProvider>
      <Grid sx={{ width: "100%" }} container>
        <Grid
          sx={{ border: "1px solid #000", padding: "0px 30px" }}
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
          item
        >
          <h2>Let's create something together</h2>
        </Grid>
        <Grid
          sx={{
            border: "1px solid #000",
            padding: "10px 30px",
            borderTop: "none",
          }}
          item
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <ThemeProvider theme={theme}>
            <TextField
              type="text"
              color={isFormDirty && !validateName() ? "error" : "primary"}
              label="* Name"
              placeholder="Chema Alonso"
              onChange={(e) => onChangeMail("name", e.target.value)}
              value={mail.name}
              fullWidth
            ></TextField>
          </ThemeProvider>
        </Grid>
        <Grid
          sx={{
            border: "1px solid #000",
            padding: "10px 30px",
            borderTop: "none",
          }}
          item
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <ThemeProvider theme={theme}>
            <TextField
              type="text"
              color={isFormDirty && !validateEmail() ? "error" : "primary"}
              label="* Email"
              placeholder="chema.alonso@company.com"
              onChange={(e) => onChangeMail("email", e.target.value)}
              value={mail.email}
              fullWidth
            ></TextField>
          </ThemeProvider>
        </Grid>
        <Grid
          sx={{
            border: "1px solid #000",
            padding: "10px 30px",
            borderTop: "none",
          }}
          item
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
        >
          <ThemeProvider theme={theme}>
            <TextField
              type="text"
              color={isFormDirty && !validateMessage() ? "error" : "primary"}
              label="* Message"
              placeholder="Say hi!"
              rows={6}
              onChange={(e) => onChangeMail("message", e.target.value)}
              value={mail.message}
              multiline
              fullWidth
            ></TextField>
          </ThemeProvider>
        </Grid>
        <Grid
          sx={{
            border: "1px solid #000",
            borderTop: "none",
            padding: "10px 30px",
          }}
          xs={12}
          sm={12}
          md={12}
          lg={12}
          xl={12}
          item
        >
          <Grid item container>
            <Grid xs={12} sm={12} md={4} lg={4} xl={4} item>
              <Button
                id="contact-submit-btn"
                variant="contained"
                sx={{ boxShadow: "none" }}
                onClick={(e) => onSubmit(e)}
                disabled={
                  emailLoadingState === LoadingState.LOADING ||
                  !validateName() ||
                  !validateEmail() ||
                  !validateMessage()
                }
                fullWidth
              >
                Submit
              </Button>
            </Grid>
            <Grid xs={0} sm={0} md={4} lg={4} xl={4} item></Grid>
            <Grid xs={0} sm={0} md={4} lg={4} xl={4} item></Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};
