import { Project, User } from "../datatypes/bindings";
import "./Body.scss";
import ListComponent from "./list-project/List";
import { BioContainer } from "./bio-container/BioContainer";
import { LandingContainer } from "./landing-container/LandingContainer";
import { TechnologyContainer } from "./technology-container/TechnologyContainer";
import { ContactContainer } from "./contact-container/ContactContainer";
import { ApiClient } from "../datatypes/apiClient";

interface BodyProps {
  projects?: Project[];
  user?: User;
  apiClient: ApiClient;
  setFilter: (projectName: string, projectTechs: string) => void;
}

const BodyComponent: React.FunctionComponent<BodyProps> = (
  props: BodyProps
) => {
  return (
    <>
      <section>
        <LandingContainer />
        <BioContainer />
        <TechnologyContainer />
        <ListComponent
          projects={props.projects?.map((p) => p as Project)}
          setFilters={props.setFilter}
        />
        <ContactContainer apiClient={props.apiClient} />
      </section>
    </>
  );
};

export default BodyComponent;
