import { Alert, Button, Grid, TextField, ThemeProvider } from "@mui/material";
import { Project } from "../../datatypes/bindings";
import ListItemComponent from "../../datatypes/gcomponents";
import { useState } from "react";
import { createTheme } from "@mui/material/styles";

interface ListProps {
  projects?: Project[];
  setFilters: (projectName: string, projectTechs: string) => void;
}

const theme = createTheme({
  palette: {
    mode: "light",
  },
});

const ListComponent: React.FunctionComponent<ListProps> = (
  props: ListProps
) => {
  const [projectName, setProjectName] = useState("");
  const [projectTechs, setProjectTechs] = useState("");
  return (
    <>
      <div id="list-component">
        <Grid spacing={0} container>
          <Grid
            sx={{ border: "1px solid #000", padding: "0px 30px" }}
            xs={12}
            sm={12}
            md={12}
            lg={12}
            item
          >
            <h2>Look what I've been doing</h2>
          </Grid>
          <Grid item container>
            <Grid
              key="project-name-filter"
              sx={{
                border: "1px solid #000",
                padding: "10px 30px",
                borderTop: "none",
              }}
              item
              md={6}
              xs={12}
              sm={12}
              lg={6}
            >
              <ThemeProvider theme={theme}>
                <TextField
                  id="project-name-filter"
                  name="project-name-filter"
                  type="text"
                  placeholder="Angry birds"
                  label="Project name"
                  onChange={(e) => setProjectName(e.target.value)}
                  fullWidth
                />
              </ThemeProvider>
            </Grid>
            <Grid
              key="project-tech-filter"
              className="project-tech-filter"
              sx={{
                border: "1px solid #000",
                padding: "10px 30px",
                borderTop: "none",
                borderLeft: "none",
              }}
              item
              md={6}
              lg={6}
              xs={12}
              sm={12}
            >
              <ThemeProvider theme={theme}>
                <TextField
                  id="project-tech-filter"
                  name="project-tech-filter"
                  type="text"
                  className="filter-field"
                  placeholder="Java, React, Typescript..."
                  label="Technologies"
                  onChange={(e) => setProjectTechs(e.target.value)}
                  fullWidth
                />
              </ThemeProvider>
            </Grid>
            <Grid
              key="filter-button-grid"
              className="filter-button-grid"
              sx={{
                border: "1px solid #000",
                padding: "10px 30px",
                borderTop: "none",
                borderRight: "none",
              }}
              item
              md={6}
              lg={6}
              sm={12}
              xs={12}
            >
              <Button
                variant="contained"
                color="primary"
                onClick={() => props.setFilters(projectName, projectTechs)}
                fullWidth
              >
                Filter
              </Button>
            </Grid>
            <Grid
              className="empty-grid"
              sx={{
                border: "1px solid #000",
                padding: "10px 30px",
                borderTop: "none",
                borderLeft: "none",
              }}
              item
              sm={6}
              md={6}
              lg={6}
            ></Grid>
          </Grid>
          <Grid
            sx={{
              border: "1px solid #000",
              padding: "10px 30px",
              borderTop: "none",
            }}
            alignItems="center"
            container
            spacing={0}
            className="list-project"
          >
            {props.projects?.length && props.projects.length > 0 ? (
              props.projects?.map((project, i) => (
                <Grid
                  key={`${project._id}-${i}`}
                  sx={{ padding: "10px" }}
                  item
                  lg={4}
                  md={6}
                  sm={12}
                  xs={12}
                >
                  <ListItemComponent key={"slider-" + i} project={project} />
                </Grid>
              ))
            ) : (
              <ThemeProvider theme={theme}>
                <Grid key="not-found" item md={12} xs={12} lg={12}>
                  <Alert severity="warning">No projects where found.</Alert>
                </Grid>
              </ThemeProvider>
            )}
          </Grid>
        </Grid>
      </div>
    </>
  );
};

export default ListComponent;
