import { Mail } from "./bindings";

export class ApiClient {
  private baseURL: string = "http://rrgzdev.us-east-1.elasticbeanstalk.com";
  private headers: { [header: string]: string } = {
    "content-type": "application/json;charset=UTF-8",
  };

  async getProjects(): Promise<Response> {
    return fetch(`${this.baseURL}/projects`, {
      method: "GET",
      headers: this.headers,
    });
  }

  async getUser(): Promise<Response> {
    return fetch(`${this.baseURL}/user/login`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        username: "Rene Rodriguez L",
        password: "pelemarley23",
      }),
    });
  }

  async getProjectsByQuery(
    projectName?: string,
    projectTechs?: string[]
  ): Promise<Response> {
    return fetch(`${this.baseURL}/projects/query`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify({
        name: projectName,
        technologies: projectTechs,
      }),
    });
  }

  async sendTalkRequest(mail: Mail): Promise<Response> {
    return fetch(`${this.baseURL}/mail/letstalk`, {
      method: "POST",
      headers: this.headers,
      body: JSON.stringify(mail),
    });
  }
}
